*, :after, :before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html, body{
  margin: 0;

  width: 100%;
  height: 100%;
  box-sizing: border-box;

  /* overflow: hidden; */
}

#root{
  height: 100%;
}
.div_Background {
  /* flex: 1; */
  border: 3px;
  border-color: red;
  background-color: blueviolet;
  inset: "0% 0% 0% 0%";
  position: absolute;
}

.div_Title {
  /* flex: 0.3; */
  height: 50px;
  border: 0;
  text-align: right;
  background-color: cornflowerblue;
}

.div_Body {
  /* flex: 0.7; */
  height: calc(100% - 50px);
  border: 0;
  width: 100%;
  text-align: center;  
  vertical-align: middle;
  background-color: cadetblue;
}